import React, { useEffect, useState } from "react";
import "../styles/Modal.css";

export default function Modal({
  isOpen,
  title,
  message,
  onClose,
  type = "info", // info | success | error | loading | finish
  isLoading = false,
  onConfirm,
  mode = "alert", // alert | confirm
}) {
  const [visible, setVisible] = useState(false);
  const [confirmLoading, setConfirmLoading] = useState(false);

  // Fade animation handler
  useEffect(() => {
    let timer;
    if (isOpen) {
      setVisible(true);
    } else {
      timer = setTimeout(() => setVisible(false), 300);
    }
    return () => clearTimeout(timer);
  }, [isOpen]);

  // Hide if fully closed
  if (!isOpen && !visible) return null;

  const modalClass = `modal-container modal-${type} ${
    isOpen ? "fade-in" : "fade-out"
  }`;

  const defaultTitle =
    type === "loading"
      ? "Memproses..."
      : type === "success"
      ? "Berhasil!"
      : type === "error"
      ? "Terjadi Kesalahan"
      : type === "finish"
      ? "Selesai"
      : "Informasi";

  // Async confirm handler
  const handleConfirm = async () => {
    if (!onConfirm) return;
    try {
      setConfirmLoading(true);
      await onConfirm();
    } finally {
      setConfirmLoading(false);
      onClose();
    }
  };

  return (
    <div
      className="modal-backdrop"
      onClick={
        type === "loading" || mode === "confirm" ? undefined : onClose
      }
    >
      <div
        className={modalClass}
        role="dialog"
        aria-modal="true"
        onClick={(e) => e.stopPropagation()}
      >
        {/* HEADER */}
        <div className="modal-header">
          <h3>{title || defaultTitle}</h3>

          {type !== "loading" && !confirmLoading && (
            <button className="modal-close-btn" onClick={onClose}>
              &times;
            </button>
          )}
        </div>

        {/* BODY */}
        <div className="modal-body">
          {/* LOADING */}
          {type === "loading" && (
            <div className="loading-section">
              <div className="spinner"></div>
              <span>{message || "Mohon tunggu sebentar..."}</span>
            </div>
          )}

          {/* SUCCESS */}
          {type === "success" && !isLoading && (
            <div className="success-content animated-success">
              <div className="success-checkmark">
                <div className="check-icon">
                  <span className="icon-line line-tip"></span>
                  <span className="icon-line line-long"></span>
                </div>
              </div>
              <p>{message || "Data berhasil diproses."}</p>
            </div>
          )}

          {/* ERROR */}
          {type === "error" && !isLoading && (
            <p className="error-message">
              {message || "Terjadi kesalahan."}
            </p>
          )}

          {/* INFO */}
          {type === "info" && !isLoading && <p>{message}</p>}

          {/* FINISH */}
          {type === "finish" && !isLoading && (
            <p>{message || "Selesai."}</p>
          )}
        </div>

        {/* FOOTER */}
        {type !== "loading" && (
          <div className="modal-footer">
            {mode === "confirm" ? (
              <>
                <button
                  className="modal-cancel-btn"
                  onClick={onClose}
                  disabled={confirmLoading}
                >
                  Batal
                </button>

                <button
                  className="modal-ok-btn"
                  onClick={handleConfirm}
                  disabled={confirmLoading}
                >
                  {confirmLoading ? "Memproses..." : "Ya"}
                </button>
              </>
            ) : (
              <button
                className="modal-ok-btn"
                onClick={onClose}
              >
                OK
              </button>
            )}
          </div>
        )}
      </div>
    </div>
  );
}
